import uniPopup from "../../components/uni-popup/uni-popup.vue"
import {
	PARTNERINFO,
	JSAPI,
	QRCODE
} from "../../utils/api.js"
var jweixin = require('jweixin-module')
var token
var curToken
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			infoData: '',
			imageshow: false,
			QRurl: null,
			ctx: '',
			canvasShow: false,
			ctx: '',
			imgeUrl: '',
			canvasHOw: true

		}
	},
	onLoad() {
		uni.showToast({
			title: '加载中...',
			icon: 'none'
		})
		this.initData()
		this.getUrlToken()
		this.getJSAPI()

	},
	onPullDownRefresh() {
		this.initData()
	},
	methods: {
		// 截取
		getQueryString(name) {
			var after = window.location.search
			if (after.indexOf('?') === -1) {}
			after = window.location.href.split("?")[1] || after.substr(1);
			if (after) {
				var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
				var r = after.match(reg)
				if (r !== null) {
					return decodeURIComponent(r[2]);
				} else {
					return null
				}
			}
		},
		// 获取当前保存token
		getUrlToken() {
			curToken = this.getQueryString('token')
			if (!curToken) {
				uni.navigateTo({
					url: "partnerJumpLogin/partnerJumpLogin"
				})
			} else {
				try {
					uni.setStorageSync('myToken', curToken)
				} catch (e) {
					//TODO handle the exception
					uni.showModal({
						content: e,
						showCancel: false
					});
				}
			}
		},
		// 初始化数据
		initData() {
			const params = {}
			this.$request.url_request(PARTNERINFO, params, 'GET', res => {
				if (JSON.parse(res.data).code == -900401) {
					uni.navigateTo({
						url: 'partnerTip/partnerTip'
					})
				}
				this.infoData = JSON.parse(res.data).data
				console.log('得到的initData=>', this.infoData)
				uni.stopPullDownRefresh()
				uni.hideToast()
			}, err => {}, true)
		},
		// 获得JSAPI
		getJSAPI() {
			var that = this
			const params = {}
			this.$request.url_request(JSAPI, params, "GET", res => {
				this.jsData = JSON.parse(res.data).data
				console.log("得到的签名", this.jsData.signature)
				jweixin.config({
						appId: this.jsData.appId,
						timestamp: this.jsData.timestamp,
						nonceStr: this.jsData.nonceStr,
						signature: this.jsData.signature,
						jsApiList: ['checkJsApi', 'updateAppMessageShareData', 'onMenuShareTimeline', 'onMenuShareAppMessage',
							'onMenuShareTimeline'
						]
					}),
					jweixin.ready(function() { //需在用户可能点击分享按钮前就先调用
						console.log("接口处理成功")
					});
				jweixin.error(function() {
					console.log("接口处理失败")
				})
			}, err => {})
		},
		// 分享按钮
		popShare() {
			this.$refs.share.open()

		},

		//这是一个封装好的方法 
		promisify: api => {
			return (options, ...params) => {
				return new Promise((resolve, reject) => {
					const extras = {
						success: resolve,
						fail: reject
					}
					api({ ...options,
						...extras
					}, ...params)
				})
			}
		},
