				_this.imageshow = true
				setTimeout(function() {
					_this.ctx.draw(true, (ret) => {
						//	开始绘制
						console.log('success', ret);
						that.tempFileImage()
						uni.hideLoading();
						// _this.finishDraw = true;
						_this.imageshow = true
					});

				}, 600);

			})

		},
		tempFileImage() {
